/* App */

* {
  margin: 0px;
  font-family: "Josefin Sans", sans-serif;
}

html {
  background-color: #1D2134;
}

main {
  margin: 0 auto;
  width: 75%;
  max-width: 1000px;
}

footer {
  background-color: #2b2f4b;
  height: 400px;
  width: 100%;
}

img {
  object-fit: contain;
  max-width: 100%;
  max-height: 100%;
}

/* Icon bar */

/* Fixed/sticky icon bar (vertically aligned 50% from the top of the screen) */
.icon-bar {
  position: fixed;
  bottom: 16px;
  left: 0;
}

/* Style the icon bar links */
.icon-bar a {
  display: block;
  text-align: center;
  padding: 8px 16px;
  transition: all 0.3s ease;
  color: white;
  font-size: 24px;
  opacity: 0.7;
}

/* Style the social media icons with color, if you want */
.icon-bar a:hover {
  opacity: 1;
}

body {
  overscroll-behavior-y: none;
}

#root {
  overflow: hidden;
}

