/* Underline link */

/* Underline styles */
.underline_link {
  display: inline-block;
  position: relative;
  padding: 0.2em 0;
  text-decoration: none;
  color: #fff;
  font-weight: bold;
  width: auto;
  transition: all 0.2s ease-in-out;
}

/* Fade in */
.underline_link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.1em;
  background-color: white;
  opacity: 0;
  transition: opacity 300ms, transform 300ms;
}

.underline_link:hover::after,
.underline_link:focus::after {
  opacity: 1;
  transform: translate3d(0, 0.2em, 0);
}

.underline_link {
  overflow: hidden;
}

.underline_link::after {
  opacity: 1;
  transform: translate3d(-100%, 0, 0);
}

.underline_link:hover::after,
.underline_link:focus::after {
  transform: translate3d(0, 0, 0);
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1;
  height: 100%;
}

.double-column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 2;
  height: 100%;
}

.SelectedWord__information {
  padding: 36px 0px;
}

@media screen and (max-width: 1000px) {
  .row {
    display: block;
  }

.SelectedWork__card {
  height: auto;
  padding: 12px 24px;
}
}

@media screen and (max-width: 730px) {
.Home {
  margin: 4em 0px;
  width: 100%;
  margin-bottom: 25vh;
}
}


@media screen and (max-width: 575px) {
ol li::before {
  display: none;
}

.SelectedWord__information {
  padding-bottom: 0px;
}

.links a {
  margin-bottom: 12px;
  font-size: 1.2em;
}
}
